@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;500&family=Rubik:wght@300;700&display=swap);
*,*::after,*::before{margin:0;padding:0;box-sizing:border-box}html{width:100%;height:100%;font-family:'Roboto', sans-serif;font-size:62.5%}@media only screen and (max-width: 75em){html{font-size:56.25%}}@media only screen and (max-width: 56.25em){html{font-size:50%}}@media only screen and (min-width: 112.5em){html{font-size:75%}}body{width:100%;height:100%;position:relative}body a,body a:hover{text-decoration:none !important}body #root{position:relative;width:100%;height:100%}


